import React, { ReactNode, useState, useCallback } from "react";
import { ThemeProvider } from "@material-ui/styles";
import { Theme, CssBaseline, useMediaQuery } from "@material-ui/core";

import Themes, { ThemeContext, ThemeMode } from "../theme";

function MuiThemeWrapper({ element }: { element: ReactNode }) {
  const prefersDarkMode = useMediaQuery("(prefers-color-scheme: dark)");
  const mode =
    typeof window !== "undefined"
      ? (localStorage.getItem("themeMode") as ThemeMode) || "dark"
      : prefersDarkMode
      ? "dark"
      : "light";
  const [localTheme, setLocalTheme] = useState({ mode: mode });
  const changeTheme = useCallback(() => {
    const mode = localTheme.mode === "light" ? "dark" : "light";
    localStorage.setItem("themeMode", mode);
    setLocalTheme({ mode: mode });
  }, [localTheme]);

  return (
    <ThemeContext.Provider value={[localTheme, changeTheme]}>
      <ThemeProvider theme={Themes[localTheme.mode]}>
        <CssBaseline />
        {element}
      </ThemeProvider>
    </ThemeContext.Provider>
  );
}

export default function wrapRootElement({ element }: { element: ReactNode }) {
  return <MuiThemeWrapper element={element}></MuiThemeWrapper>;
}
