import { createMuiTheme, responsiveFontSizes } from "@material-ui/core/styles";
import createPalette from "@material-ui/core/styles/createPalette";
import arrayToLiterals from "./utils/array-to-literals";
import React from "react";

const Modes = arrayToLiterals(["dark", "light"]);
export type ThemeMode = typeof Modes[number];
export type LocalTheme = {
  mode: ThemeMode;
};
export const CurrentTheme: LocalTheme = { mode: Modes[0] };
export const ThemeContext = React.createContext<[LocalTheme, () => void]>([
  CurrentTheme,
  () => {
    return;
  },
]);

const themes = {
  light: responsiveFontSizes(
    createMuiTheme({
      palette: createPalette({
        type: "light",
        common: { white: "#F5F5F5" },
        primary: {
          main: "#1284fe",
        },
      }),
    }),
  ),
  dark: responsiveFontSizes(
    createMuiTheme({
      palette: createPalette({
        type: "dark",
        common: { white: "#F5F5F5" },
        background: {
          default: "#131217",
          paper: "#191a1f",
        },
        // primary: {
        //   main: "#3288d6",
        // },
        secondary: {
          main: "#48c295",
        },
        error: {
          main: "#e84234",
        },
      }),
    }),
  ),
};

export default themes;
